import { registerBlockType } from "@wordpress/blocks";
const { RichText } = wp.blockEditor;

registerBlockType("playbook/collapse-header", {
  title: "collapse-header",
  icon: "heading",
  category: "playbook",
  parent: [ "playbook/collapse" ],
  attributes: {
    content: {
      source: "html",
      selector: "h3"
    },
    textColor: {
      type: "string",
      default: "#000000"
    }
  },
 
  edit: props => {
    const {
      attributes: { content, textColor },
      setAttributes,
      className
    } = props;
    return (
        <RichText
          tagName="h5"
          value={content}
          placeholder="Write your collapse heading here"
          role="heading" 
          aria-level="2" 
          style={{
            color: textColor
          }}
          onChange={newContent => {
            setAttributes({ content: newContent });
          }}
        />
    );
  },
  save: props => {
    const {
      attributes: { content, textColor },
      setAttributes,
      className
    } = props;
    return (
      
      <button 
        type="button" 
        aria-expanded="false" 
        className="collapse-header alignwide"
      >
        <RichText.Content
        tagName="h3"
        className="alignwide"
        value={content}
        aria-label="title for collapse content"
        role="heading" 
        style={{
          color: textColor
        }}
      />
      </button>
    );
  }
});