import { registerBlockType } from "@wordpress/blocks";
import { Fragment } from "@wordpress/element";
import { InnerBlocks, useBlockProps } from "@wordpress/block-editor";
import * as fog from '../../../helsinki-design-system/packages/design-tokens/tokens/color/brand/fog.json';

import './style.scss';
import './editor.scss';

const el = wp.element.createElement;
const iconEl = el('svg', { width: 20, height: 20 },
  el('path', { d: "M0,800h20V0c-4.9,0-5,2.6-9.9,2.6S5,0,0,0V800z", fill: fog.color.fog.value } )
);


// const collapseListTemplate = [
//   [ 'core/heading', { level: 2, className: 'collapse-list-header alignwide', placeholder: 'List title' } ],
//   [ 'playbook/collapse', {}, [
//     [ 'core/button', {}, [
//       [ 'core/heading', { level: 5, className: 'collapse-header alignwide', placeholder: 'Collapse List Title' } ],
//     ] ],
//     [ 'core/paragraph', { placeholder: 'Content' } ],
//   ] ],
// ];

const collapseListTemplate = [
  [ 'core/heading', { level: 2, className: 'collapse-list-header alignwide', placeholder: 'List title' } ],
  [ 'playbook/collapse', {}],
];

const blockStyle = {
  backgroundColor: '#900',
  color: '#f0f',
  padding: '20px',
};


registerBlockType("playbook/collapse-list", {
  title: "Collapse-list",
  icon: "list-view",
  category: "playbook",
  attributes: {
    backgroundColor: {
      type: "string",
      default: '#ff00ff'
    },
  },
 
  edit: props => {
    const {
      className,
    } = props;
    // const blockProps = useBlockProps();
    // const blockProps = useBlockProps( { style: blockStyle } );
    const blockProps = useBlockProps();
    const ALLOWED_BLOCKS = [ 'playbook/collapse', 'core/separator' ];

    return (
      <Fragment>
        <div { ...blockProps } className={ 'alignWide' } >
          <div className="collapse-list">
            <InnerBlocks
              template={ collapseListTemplate }
              allowedBlocks={ ALLOWED_BLOCKS }
            />
          </div>
        </div>
      </Fragment>
    );
  },
  save: props => {
    // const {
    //   InnerBlocks
    // } = props;
    // const blockProps = useBlockProps.save( { style: blockStyle } );
    const blockProps = useBlockProps.save();
    return (
      <div { ...blockProps }>
          <div className="collapse-list">
            <InnerBlocks.Content />
          </div>
      </div>
    );
  }
});