import { registerBlockType } from "@wordpress/blocks";
import { InnerBlocks, useBlockProps } from "@wordpress/block-editor";


const collapseTemplate = [
  [ 'core/group', {}, [
    [ 'playbook/collapse-header'],
    [ 'core/group', { className: 'collapse-content alignwide' }, [
      [ 'core/paragraph', { className: 'alignwide', placeholder: 'Content' } ],
    ] ],
  ] ],
];

registerBlockType("playbook/collapse", {
  title: "collapse",
  icon: "media-document",
  category: "playbook",
  parent: [ "playbook/collapse-list" ],
  attributes: {
    backgroundColor: {
      type: "string",
      default: '#ff0000'
    },
  },
 
  edit: props => {
    const blockProps = useBlockProps();
    return (

      <div { ...blockProps }>
        <div className="collapse">
        {/* <Button /> */}
          <InnerBlocks
            template={ collapseTemplate }
          />
        </div>
      </div>

    );
  },
  save: props => {
    const blockProps = useBlockProps.save();
    return (
      <div { ...blockProps }>
          <div className="collapse" role="region">
            <InnerBlocks.Content />
          </div>
      </div>
    );
  }
});