const { __ } = wp.i18n;
import { registerBlockType } from "@wordpress/blocks";
import { Fragment } from "@wordpress/element";
import { InnerBlocks, useBlockProps } from "@wordpress/block-editor";
import { Button } from '@wordpress/components';
import { MediaUpload, MediaUploadCheck } from '@wordpress/block-editor';
import { select, withSelect } from '@wordpress/data';
import { addFilter } from '@wordpress/hooks';
const { RichText, InspectorControls } = wp.blockEditor;
const { ToggleControl, PanelBody, PanelRow, CheckboxControl, SelectControl, ColorPicker, Spinner, ResponsiveWrapper } = wp.components;

import * as fog from '../../../helsinki-design-system/packages/design-tokens/tokens/color/brand/fog.json';

import './style.scss';

const el = wp.element.createElement;
const iconEl = el('svg', { width: 24, height: 24 },
  el('path', { d: "M18.7 3H5.3C4 3 3 4 3 5.3v13.4C3 20 4 21 5.3 21h13.4c1.3 0 2.3-1 2.3-2.3V5.3C21 4 20 3 18.7 3zm.8 15.7c0 .4-.4.8-.8.8H5.3c-.4 0-.8-.4-.8-.8V5.3c0-.4.4-.8.8-.8h6.2v8.9l2.5-3.1 2.5 3.1V4.5h2.2c.4 0 .8.4.8.8v13.4z", fill: fog.color.fog.value } )
);

const heroTemplate = [
  [ 'core/group', { id: "hero", className: 'alignfull', dataAlign: 'full' }, [
    [ 'core/columns', {}, [
      [ 'core/column', {}, [
        [ 'core/heading', { level: 1, className: 'hero-header alignwide', placeholder: 'Hero title h1' } ],
        [ 'core/paragraph', {placeholder: "Content"}],
      ] ],
      [ 'core/column', {}, [
        [ 'core/image', {}],
      ] ],
    ] ],
  ] ],
];

const modifyDefaultAlignment = (settings, name) => {
  if (name !== 'core/group') {
    return settings;
  }
  const newSettings = {
    ...settings,
    attributes: {
      ...settings.attributes,
      align: { type: 'string', default: 'full' },
    },
  };
  return newSettings;
};

addFilter(
  'blocks.registerBlockType',
  'playbook-blocks/hero',
  modifyDefaultAlignment,
);


registerBlockType("playbook/hero", {
  title: "hero",
  icon: iconEl,
  category: "playbook",
  supports: { align: true },
  attributes: {
    myRichHeading: {
      type: 'string',
    },
    myRichText: {
      type: 'string',
      source: 'html',
      selector: 'p'
    },
    toggle: {
      type: 'boolean',
      default: true
    },
    favoriteAnimal: {
      type: 'string',
      default: 'dogs'
    },
    favoriteColor: {
      type: 'string',
      default: '#DDDDDD'
    },
    activateLasers: {
      type: 'boolean',
      default: false
    },
    bgImageId: {
        type: 'number',
        default: 1020
    },
    bgImageUrl: {
        type: 'string',
        default: 'https://eqsign.fi/test/wp-content/themes/playbook/assets/images/hero-bg-2.svg',
    },
  },
  getEditWrapperProps() {
    return {
      'data-align': 'full',
    };
  },
 
  edit: props => {
    const {
      className, supports, attributes, setAttributes} = props;
    props.setAttributes({ align: 'full' });
    const blockProps = useBlockProps();
    const ALLOWED_BLOCKS = [ 'playbook/hero-content', 'core/separator' ];
    const ALLOWED_MEDIA_TYPES = [ 'image' ];
    const { bgImageId, bgImageUrl } = attributes;
    const onUpdateImage = ( image ) => {
      setAttributes( {
        bgImageId: image.id,
        bgImageUrl: image.url,
      } );
    };
    const onRemoveImage = () => {
      setAttributes( {
        bgImageId: undefined,
        bgImageUrl: undefined,
      } );
    };
    const instructions = <p>{ __( 'To edit the background image, you need permission to upload media.', 'playbook' ) }</p>;

    console.log("bgImageUrl: ", bgImageUrl);

    return (
      <Fragment>
        <InspectorControls>
          <PanelBody
            title="Playbook hero settings"
            initialOpen={true}
          >
            <PanelRow>
              <div className="wp-block-hero-bg-image-selector">

                <MediaUploadCheck fallback={ instructions }>
                  <MediaUpload
                    title={ __( 'Background image', 'playbook' ) }
                    onSelect={ onUpdateImage }
                    allowedTypes={ ALLOWED_MEDIA_TYPES }
                    value={ bgImageId }
                    render={ ( { open } ) => (
                      <Button
                        className={ ! bgImageId ? 'editor-post-featured-image__toggle' : 'editor-post-featured-image__preview' }
                        onClick={ open }>
                        { ! bgImageId && ( __( 'Set background image', 'playbook' ) ) }
                        { !! bgImageId && ! bgImageUrl && <Spinner /> }
                        { bgImageUrl &&
                          <img src={ bgImageUrl } alt="Hero background" />
                        }
                      </Button>
                    ) }
                  />
                </MediaUploadCheck>

                { !! bgImageId && bgImageUrl &&
                  <MediaUploadCheck>
                    <MediaUpload
                      title={ __( 'Background image', 'playbook' ) }
                      onSelect={ onUpdateImage }
                      allowedTypes={ ALLOWED_MEDIA_TYPES }
                      value={ bgImageId }
                      render={ ( { open } ) => (
                        <Button onClick={ open } isDefault isLarge>
                          { __( 'Replace background image', 'playbook' ) }
                        </Button>
                      ) }
                    />
                  </MediaUploadCheck>
                }
                { !! bgImageId &&
                  <MediaUploadCheck>
                    <Button onClick={ onRemoveImage } isLink isDestructive>
                        { __( 'Remove background image', 'playbook' ) }
                    </Button>
                  </MediaUploadCheck>
                }
              </div>
            </PanelRow>

            <PanelRow>
              <ToggleControl
                label="Toggle me"
                checked={attributes.toggle}
                onChange={(newval) => setAttributes({ toggle: newval })}
              />
            </PanelRow>
            <PanelRow>
              <SelectControl
                label="What's your favorite animal?"
                value={attributes.favoriteAnimal}
                options={[
                  {label: "Dogs", value: 'dogs'},
                  {label: "Cats", value: 'cats'},
                  {label: "Something else", value: 'weird_one'},
                ]}
                onChange={(newval) => setAttributes({ favoriteAnimal: newval })}
              />
            </PanelRow>
            <PanelRow>
              <ColorPicker
                color={attributes.favoriteColor}
                onChangeComplete={(newval) => setAttributes({ favoriteColor: newval.hex })}
                disableAlpha
              />
            </PanelRow>
            <PanelRow>
              <CheckboxControl
                label="Activate lasers?"
                checked={attributes.activateLasers}
                onChange={(newval) => setAttributes({ activateLasers: newval })}
              />
            </PanelRow>

          </PanelBody>
        </InspectorControls>

        <div 
          { ...blockProps } 
          className={ 'alignWide wp-block-playbook-hero' } 
          style={{
            backgroundImage: `url(${ bgImageUrl })`,
        }} >
          <div className="hero">
            <InnerBlocks
              template={ heroTemplate }
              allowedBlocks={ ALLOWED_BLOCKS }
            />
          </div>
        </div>
      </Fragment>
    );
  },
  save: props => {
    const { className, supports, attributes, setAttributes} = props;
    const { bgImageUrl } = attributes;
    const blockProps = useBlockProps.save();

    return (
      <div 
        { ...blockProps } 
        style={{
          backgroundImage: `url(${ bgImageUrl })`
        }} 
        >
          <div className="hero" id="hero">
            <InnerBlocks.Content />
          </div>
      </div>
    );
  }
});